import httpGet from '../httpGet/httpGet';


export function actionLocalStorage(localStorageList){
    localStorage.setItem('citiesFavoritList', localStorageList)
    return {
        type: 'LOCALSTORAGE_INC',
        localStorageList
    }
};

export function getCities() {
    return async function (dispatch){    
        try { 
            await fetch("https://raw.githubusercontent.com/David-Haim/CountriesToCitiesJSON/master/countriesToCities.json")
                .then(response => {
                    response.json().then( response => {
                        dispatch(actionCountries(response))
                   })
                })
        }      
        catch (e) {
            console.log(e)
        }
    }
};

export  function actionGeoPosition(position){    
    return {
        type: 'POSITION_INC',
        position
    }
};

export function actionCountries(payload){
    return {
        type: 'COUNTRIES_INC',
        payload       
    }
};

export function actionCountrySel(country, cities){
    return {
        type: 'COUNTRIES_SELECT',
        country: country,
        payload: cities,
        status: false       
    }
};

export function actionCitySel(city){
    return {
        type: 'CITY_SELECT',
        payload: city,

    }
};

export function actionCityForecast(payload, timesOfDay){
    return {
        type: 'CITYFORECAST_INC',
        payload,
        timesOfDay
    }
};

export function getWeatherForecast(city) {
    return async function (dispatch){    
        try { 
            await httpGet(city)
             .then(result => {                    
                    let sunrise = result.current_observation.astronomy.sunrise.split(' ')
                    let sunset = result.current_observation.astronomy.sunset.split(' ')
                    let day = new Date(result.current_observation.pubDate*1000).toString().split(' ')
                    let date = day.splice(1, 3)
                    let dateMilliseconds = result.current_observation.pubDate*1000
                    let sunriseDay = new Date(date.concat(sunrise).join(' ')).getTime()
                    let sunsetDay =new Date(date.concat(sunset).join(' ')).getTime()
                    let timesOfDay = sunriseDay <= dateMilliseconds && dateMilliseconds <=sunsetDay?  'd':'n';
                    dispatch(actionCityForecast(result, timesOfDay))
                })            
        }
        catch (e) {
            console.log(e)
        }
    }
}
