import React, {Component} from 'react'
import ConnectedSelectCountry from './SelectCountries'
import ConnectedSelectCity from './SelectSity'


class Header extends Component {
    render() {
        return (
            <div className="header">            
                <img className="logoImg" src={require("../../images/WeatherLogo.png")} alt="images/logo.png"/>
                
                 <div className="search">
                    <ConnectedSelectCountry/>
                    <ConnectedSelectCity/>
                 </div>

            </div>
        );
    }
}

export default Header
