import React, {Component} from 'react'
import {connect} from 'react-redux'
import {getCities} from '../../actions/actions'


class DetailsAll extends Component {
	constructor (props) {
		super(props)
		this.state = {
			isLoading:true			
		} 
	};
	time(srt) {
		let arr = srt.split(' ')		
		let d = new Date("1/1/2013 " + arr[0] + ' '+ arr[1]);
		let minutes = d.getMinutes()
		minutes = minutes<10? '0'+minutes: minutes;
		let time = d.getHours() + ':' + minutes;
		return (time)
	};
	degToCompass(num) {
    	let val = Math.floor((num / 22.5) + 0.5);
    	let arr = ["N", "NNE", "NE", "ENE", "E", "ESE", "SE", "SSE", "S", "SSW", "SW", "WSW", "W", "WNW", "NW", "NNW"];
    	return arr[(val % 16)];
	};
	
    render() {
    	this.state.isLoading = this.props.isLoading
    	if(this.state.isLoading) return (<div><i className="fa fa-spinner fa-spin"></i></div>)
    	let info = this.props.forecastAll
    	let urlGif =  `https://s.yimg.com/zz/combo?a/i/us/nws/weather/gr/${info.current_observation.condition.code}${this.props.timesOfDay}.png`
	
		return (
            <div className="details"  key={Math.random()}>               
                <div className="divh2">
                 	<h2>Details</h2>
                </div>
				<div className="detAll">
					<div className="detImg">
	                 	<img src= {urlGif} alt='gif'/>
	                </div>
	                <div className="detMain">
						<div className="feelsLike">
							<div className="">Feels like</div>
							<div className="">{((info.current_observation.wind.chill - 32)*5/9).toFixed()}째</div>
						</div>	                	
						<div className="humidity">
							<div className="">Humidity</div>
							<div className="">{info.current_observation.atmosphere.humidity}%</div>
						</div>
						<div className="visibility">
							<div className="">Visibility</div>
							<div className="">{(info.current_observation.atmosphere.visibility/1.61).toFixed(2)}km</div>
						</div>
						<div className="indexUV">
							<div className="">UV Index</div>
							<div className="">{info.current_observation.atmosphere.rising}</div>
						</div>
						<div className="sunrise">
							<div className="">Sunrise</div>
							<div className="">{this.time(info.current_observation.astronomy.sunrise)}</div>
						</div>
						<div className="sunset">
							<div className="">Sunset</div>
							<div className="">{this.time(info.current_observation.astronomy.sunset)}</div>
						</div>						
					</div>	
				</div>
			</div>
        );
    }
};
let mapStateToProps = state => ({forecastAll: state.forecastReducer.forecastAll, timesOfDay: state.forecastReducer.timesOfDay, isLoading: state.forecastReducer.isLoading});  
let mapDispatchToProps = {getCities};
let Details = connect(mapStateToProps,mapDispatchToProps)(DetailsAll);

export default Details;