import React, {Component} from 'react';
import { connect } from 'react-redux';
import { actionLocalStorage, getWeatherForecast } from '../../actions/actions';


class MyFavorites extends Component {
    constructor (props) {
        super(props)
        this.state = {
            isLoading:true,
            localStorageList:this.props.localStorageList  
        }
    };   
    btnMyGeoPosition() {
        this.props.getWeatherForecast(localStorage.getItem('myCity'))
    };

    btnAddCity() {
        let arr = this.state.localStorageList.length !== 0 ? JSON.parse(this.state.localStorageList): [];
        if (arr.some(x=> x===this.props.forecastAll.location.city)) return    
        arr.push(this.props.forecastAll.location.city)
        this.setState.localStorageList = JSON.stringify(arr)
        this.props.actionLocalStorage(JSON.stringify(arr))
    };
    btnDeleteCity() {
        let arr = JSON.parse(this.state.localStorageList)
        let city = this.props.forecastAll.location.city
        let  newArr = arr.filter(x=> x!==city)
        this.setState.localStorageList = JSON.stringify(newArr)
        this.props.actionLocalStorage(JSON.stringify(newArr))  
    };

    render() {
        this.state.isLoading = this.props.isLoading
        if(this.state.isLoading) return (<div><i className="fa fa-spinner fa-spin"></i></div>)
        
        this.state.localStorageList = this.props.localStorageList
        let arrCity =[]
        if (this.state.localStorageList.length !== 0)  {arrCity = JSON.parse(this.state.localStorageList)} 
        
        return (
            <div className="myLocations" key={Math.random()}>
                <aside>
                    <div className="favorit divh2">
                        <h2>My favorites places</h2>
                    </div>
                    <div className="divh2 myGeo"><button className="btnLoc" onClick={this.btnMyGeoPosition.bind(this)}>My geo position</button></div>
                    <div>
                        <p>Click the button to add cities to the list.</p>
                        <div className="favorit" key={Math.random()}>
                           <div className="divh2"><button className="btnLoc" onClick={this.btnAddCity.bind(this)}>Add</button></div>
                            <div className="favorit citiesList" onClick = {(event)=> this.props.getWeatherForecast(event.target.innerText)}>{arrCity.map((x,)=> <p className="linkWorld" key={Math.random()}>{x}</p>)}</div>
                            <div className="divh2"><button className="btnLoc" onClick={this.btnDeleteCity.bind(this)}>Delete</button></div>
                        </div>
                        <div className="divh2"><h2>Around the World</h2></div>
                        <div className="citiesFavoritList"></div>
                        <div className="favorit citiesList" onClick = {(event)=> this.props.getWeatherForecast(event.target.innerText)}>                        
                            <p className="linkWorld">New York</p>
                            <p className="linkWorld">Los Angeles</p>
                            <p className="linkWorld">Chicago</p>
                            <p className="linkWorld">Houston</p>
                            <p className="linkWorld">Philadelphia</p>
                            <p className="linkWorld">San Francisco</p>
                            <p className="linkWorld">Mexico City</p>
                            <p className="linkWorld">Tokyo</p>
                            <p className="linkWorld">Sao Paulo</p>
                            <p className="linkWorld">London</p>
                            <p className="linkWorld">Paris</p>
                            <p className="linkWorld">Venice</p>
                        </div>
                    </div>    
                </aside>
            </div>
        )
    }
};
let mapStateToProps = state => ({forecastAll: state.forecastReducer.forecastAll, 
    timesOfDay: state.forecastReducer.timesOfDay, 
    isLoading: state.forecastReducer.isLoading,
    localStorageList: state.localStorageReducer.localStorageList,
    position:state.geoPositionReducer.position})  
let mapDispatchToProps = {actionLocalStorage, getWeatherForecast};     

 let MyLocations = connect(mapStateToProps,mapDispatchToProps)(MyFavorites); 




export default MyLocations;