import React, {Component} from 'react'
import { connect } from 'react-redux'
import { compose, withProps } from "recompose"
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"




const MyMapComponent = compose(
  withProps({
    googleMapURL: "https://maps.googleapis.com/maps/api/js?hl=en&v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div  className="loadingElement"  />,
    containerElement: <div className="containerElement"  />,
    mapElement: <div className="mapElement"  />,
  }),
  withScriptjs,
  withGoogleMap
)((props) =>
  <GoogleMap
    defaultZoom={4}
    center= {props.center}
  >
    {props.isMarkerShown && <Marker position={props.center} onClick={props.onMarkerClick} />}
  </GoogleMap>
)

class MyComponent extends Component {
 
  state = {
    isLoading:true,
  } 

  componentDidMount() {
    this.delayedShowMarker()  
  }

  delayedShowMarker = () => {
    setTimeout(() => {
      this.setState({ isMarkerShown: true })
    }, 3000)
  }

  handleMarkerClick = () => {
    this.setState({ isMarkerShown: false })
    this.delayedShowMarker()
  }

  render() {
    this.state.isLoading = this.props.isLoading
    if(this.state.isLoading) return (<div><i className="fa fa-spinner fa-spin"></i></div>)
    
    return (      <MyMapComponent
        isMarkerShown={this.state.isMarkerShown}
        onMarkerClick={this.handleMarkerClick}
        center={{ lat:Number(this.props.forecastAll.location.lat), lng:Number(this.props.forecastAll.location.long)}}
      
      />
    )
  }
}
 	let mapStateToProps = state => ({forecastAll: state.forecastReducer.forecastAll, isLoading: state.forecastReducer.isLoading})  
  let mapDispatchToProps = {};
  let MyFancyComponent = connect(mapStateToProps,mapDispatchToProps)(MyComponent) 

export default MyFancyComponent


