import React, {Component} from 'react'
import Select from 'react-select';
import { connect } from 'react-redux'
import { getCities, actionCountrySel } from '../../actions/actions'


class SelectCont extends Component {
	constructor (props) {
		super(props)
		this.state = {
			isLoading:true			
		}
	}
	
	

	componentDidMount() {
		this.props.getCities()		
	}

	render() {	
		if (!this.props.response) return (<p>Loading ... </p>)
		let res = this.props.response	
        let countries = []
        for(let key in res){countries.push({label: String(key), value:String(key)})}
        return (
			<div className='select'>	
				<Select placeholder="Select Country" options={countries} onChange={opt => {let cities = this.props.response[opt.value];
					this.props.actionCountrySel(opt.value, cities)}}  />
			</div>						
			
		)
	}
}


let mapStateToProps = state => ({response: state.responseReducer.response})  
let mapDispatchToProps = {getCities, actionCountrySel};     

 let ConnectedSelectCountry = connect(mapStateToProps,mapDispatchToProps)(SelectCont) 

 export default ConnectedSelectCountry

