import React, {Component} from 'react';

class ForecastDay extends Component {

    srcGet(code) {
        return `https://s.yimg.com/zz/combo?a/i/us/nws/weather/gr/${code}d.png`
    }
    render() {
        let date = new Date(this.props.forecast.date*1000).toString().split(' ').slice(0, 4);
        let dateNow = new Date(this.props.forecast.date*1000).getDate();
        let dateDay = date[0]+' '+dateNow+' '+date[1]+' '+date[3];

        return (
            <div className="forecastDay">
                <div className="day">{dateDay}</div>
                <img className="forImg" src={this.srcGet(this.props.forecast.code)} alt="gif"/>  
                <div className="forText">{this.props.forecast.text}</div>
                <div className="forHigh">{this.props.forecast.high} </div>
                <div className="forLow">{this.props.forecast.low} </div>
            </div>
        );
    }
}
export default ForecastDay;