import React, { Component } from 'react';
import {Router, Route, Link, Switch} from 'react-router-dom';
import createHistory from "history/createBrowserHistory";
import 'font-awesome/css/font-awesome.min.css';

import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHome, faLongArrowAltDown } from '@fortawesome/free-solid-svg-icons'
import './App.css';
import Header from './components/componentsHeader/Header.js'
import Main from './components/componentsMain/Main'
import MyFavoritCitySearch from './components/componentsSearch/MyFavoritCitySearch'

library.add(faHome, faLongArrowAltDown)

class App extends Component {
  render() {
    return (     
        <div className="App">
          <LinkRouter />
        </div>
     
    );
  }
}


class LinkRouter extends Component {
   render() {
      return (
         <Router history={createHistory()}>
            <div>
                <ul className='nav'>
                    <li><Link to="/"><FontAwesomeIcon icon="home" className='home'/>Home</Link></li>
                    <li><Link to='/searchCity'>Add cities to favorites</Link></li>
                </ul>
                <Switch>
                  <Route exact path="/" component={All} />               
                  <Route exact path="/searchCity" component={MyFavoritCity}/>                  
                </Switch>
            </div>
         </Router>
      ); 
    } 
  }


class All extends Component {
    render() {
        return (
            <div className="App">
                <Header/>
                <Main/>              
            </div>
        );
    }
}


const MyFavoritCity = () => {
  return (
    <div className="App">
      <Header/>
      <MyFavoritCitySearch/>              
    </div>

  )} 











export default App;