export let responseReducer = (state, action) => { 
    if (state === undefined){ 
        return {response: {}, country: '', cities: [], city: '', status: true}  
    }
    if (action.type === 'COUNTRIES_INC'){
        return Object.assign({}, state, {response: action.payload})  
    }
    if (action.type === 'COUNTRIES_SELECT'){       
        return Object.assign({}, state, {country: action.country, cities: action.payload, status: action.status}) 
    }
    if (action.type === 'CITY_SELECT'){       
        return Object.assign({}, state, {city: action.payload}) 
    }
    return state;  
};

export let forecastReducer = (state, action) => { 
    if (state === undefined){ 
        return {forecastAll: {}, timesOfDay: '', isLoading: true}  
    }
    if (action.type === 'CITYFORECAST_INC'){
        return Object.assign({}, state, {forecastAll: action.payload, timesOfDay: action.timesOfDay, isLoading: false})  
    }
    return state;  
};

export let localStorageReducer = (state, action) => { 
    if (state === undefined){
        let localStorageList
        if(localStorage['citiesFavoritList']) {
            localStorageList =  localStorage['citiesFavoritList']   
        } else {
            localStorage.setItem('citiesFavoritList', '[]');
            localStorageList = localStorage['citiesFavoritList'] 
        }
         
        
        return {localStorageList: localStorageList}  
    }
    if (action.type === 'LOCALSTORAGE_INC'){
        return Object.assign({}, state, {localStorageList: action.localStorageList})  
    } 
    return state;  
};

export let geoPositionReducer = (state, action) => { 
    if (state === undefined){ 
        return {position: {}}  
    }
    if (action.type === 'POSITION_INC'){
        return Object.assign({}, state, {position: action.position})  
    } 
    return state;  
};