import React, {Component} from 'react';
import ForecastDay from './ForecastDay';
import { connect } from 'react-redux';

class ForecastAll extends Component {
	constructor (props) {
		super(props)
		this.state = {
			isLoading: true			
		} 
	}
    render() {
		this.state.isLoading = this.props.isLoading
    	if(this.state.isLoading) return (<div><i className="fa fa-spinner fa-spin"></i></div>)
        let info = this.props.forecastAll         
		return (
            <div className="forecast"key={Math.random()}>
                <div className="divh2">
                    <h2>Forecast</h2>                   
                </div>
                {info.forecasts.map((x, i) => <ForecastDay forecast={x} key={i}/>)}
            </div>
        );
    }
}

let mapStateToProps = state => ({forecastAll: state.forecastReducer.forecastAll, isLoading: state.forecastReducer.isLoading});  
let mapDispatchToProps = {};
let Forecast = connect(mapStateToProps,mapDispatchToProps)(ForecastAll);

export default Forecast