import React, {Component} from 'react'
import Select from 'react-select';
import { connect } from 'react-redux'
import { actionCitySel, getWeatherForecast } from '../../actions/actions'


class SelectCity extends Component {
	constructor (props) {
		super(props)
		this.state = {
			status: true			
		}
	}
	componentWillMount() {
		this.props.getWeatherForecast("Kharkiv")		
	}

	render() {
		let cities = []
		let res = this.props.cities
		for(let key of res){cities.push({label: String(key), value:String(key)})}
		this.setState.status = this.props.status		 
		return (
			<div className='select'>			
				<Select placeholder="Select city" onChange={opt=>{this.props.actionCitySel(opt.value); this.props.getWeatherForecast(opt.value)}}  options={cities}/>
			</div>						
		)
	}
}

let mapStateToProps = state => ({cities: state.responseReducer.cities, status: state.responseReducer.status})  
let mapDispatchToProps = {actionCitySel, getWeatherForecast};
let ConnectedSelectCity = connect(mapStateToProps,mapDispatchToProps)(SelectCity) 

export default ConnectedSelectCity

