import React, {Component} from 'react';
import { connect } from 'react-redux';
import { getCities } from '../../actions/actions';


class InformationCity extends Component {
	constructor (props) {
		super(props)
		this.state = {
			isLoading:true			
		} 
	}

	dateis(time) { 
		let date = new Date().toString().split(' ').slice(0, 5)
		date[4] = date[4].split(':')
		date[4].pop()
		date[4] =  date[4].join(':')
		date = date[0]+' '+date[2]+' '+date[1]+' '+date[3]+' '+date[4]
		return (date)
	}

    render() {
    	this.state.isLoading = this.props.isLoading
    	if(this.state.isLoading) return (<div><i className="fa fa-spinner fa-spin"></i></div>)
    	let info = this.props.forecastAll
    	let urlImg = `https://s.yimg.com/zz/combo?a/i/us/nws/weather/gr/${info.current_observation.condition.code}${this.props.timesOfDay}.png`
		return (
            <div className="generalInformationCity" key={Math.random()}>
                <div className="city">{info.location.city}</div>
                <h2 className="country">{info.location.country}</h2>
                <p className="dateTime">{this.dateis(info.current_observation.pubDate)}</p>
                <p className="weatherCharacter"><img src= {urlImg} alt="gif"/>{info.current_observation.condition.text}</p>
                <div className="tempHighLow">
	                <div className="tempHigh"><i className="fa fa-long-arrow-up"></i>{info.forecasts[0].high}째</div>
					<div className="tempLow"><i className="fa fa-long-arrow-down"></i>{info.forecasts[0].low}째</div>			
                </div>
				<div className="temperatureIsNow">{info.current_observation.condition.temperature}<span className="tempIsNowSpan">째</span></div>
            </div>
        );
    }
}

let mapStateToProps = state => ({forecastAll: state.forecastReducer.forecastAll, timesOfDay: state.forecastReducer.timesOfDay, isLoading: state.forecastReducer.isLoading})  
let mapDispatchToProps = {getCities};     
let GneralInformationCity = connect(mapStateToProps,mapDispatchToProps)(InformationCity) 

export default GneralInformationCity




